import api from '../../api/index'
export default {
    name:'userManegement',
    data(){
        return{
            inputName:'',
            inputSex:'',
            inputTel:'',
            inputlogin:'',
            inputPass:'',
            dialogVisible:false,
            tableData: [],
            currentPage:1,
            pagesize:10,
            total:0,
        }
    },
    created(){
        this.getUser();
    },
    methods:{
        // 获取用户信息
        getUser(){
            this.tableData = [];
            let page = {
                page:this.currentPage,
                rows:this.pagesize
            }
            api.getUserInfo(page).then(res=>{
                console.log(res.obj.list)
                for(let i of res.obj.list){
                    if(i.sex == 1){
                        i.sex = "男"
                    }else if(i.sex == 0){
                        i.sex = "女"
                    }
                    this.tableData.push(i)
                }
                this.total = res.obj.total;
            })
        },
        // 删除用户信息
        del(id){
            let _this = this;
            let userId = {
                userid:id.id
            }
            console.log()
            api.deleteUser(userId).then(res=>{
                console.log(res)
                if(res.msg == "禁用成功！"){
                    this.$message({
                        showClose: true,
                        message: '删除成功',
                        type: 'success'
                    })
                }
                
                _this.getUser();
            }).catch(err=>{
                console.error(err);
            })
        },
        // 打开二级窗口
        add(){
            this.dialogVisible = true;
        },
        // 增加用户信息
        addUser(){
            let that = this;
            if(this.inputSex != '男' && this.inputSex != '女'){
                this.$message({
                    showClose: true,
                    message: '请输入男或女',
                    type: 'warning'
                })
                return;
            }
            console.log(this.inputSex)
            if(!(/^1[34578]\d{9}$/.test(this.inputTel))){
                this.$message({
                    showClose:true,
                    message:'请输入正确的手机号',
                    type: 'warning'
                })
                return;
            }
            if(this.inputSex == '女'){
                this.inputSex = 0
            }else if(this.inputSex == '男'){
                this.inputSex = 1
            }
            console.log(this.inputSex)
            let addUserInfo = {
                username:this.inputlogin,
                password:this.inputPass,
                name:this.inputName,
                tel:this.inputTel,
                sex:this.inputSex
            }
            api.addUser(addUserInfo).then(res=>{   
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'success'
                })
                that.getUser();
            })
            this.dialogVisible = false;
            this.inputName = '';
            this.inputSex = '';
            this.inputTel = '';
            this.inputlogin = '';
            this.inputPass = '';
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagesize = val;
            this.getUser();
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
             this.getUser();
        },
        tableRowClassName({row, rowIndex}) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'success-row';
            }
            return '';
        }
    }
}



